<template>
  <div>
    <!-- Loader -->
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Navbar Start -->
    <b-navbar id="navbar" fixed="top">
      <b-navbar-brand href="#" id="navbarBrand" to="/">
        <img :src="brandIcon" id="brandImg"><span>EDOTCO</span>
      </b-navbar-brand>
      <b-navbar-nav class="ml-auto">
        <b-button class="logOutBtn" variant="danger" v-if="signin" @click="removeUser" to="/">LOG OUT</b-button>
      </b-navbar-nav>
    </b-navbar>
    <!-- Navbar End -->
    <div id="mainBody" :style="{ backgroundImage: 'url(' + bannerImg + ')' }">
      <div id="mainContainer">
        <div class="logCard" v-if="!signin">
          <div class="logCardContainer">
            <div class="logCardTitle">SIGN IN</div>
            <b-form @submit="onSubmit" class="logCardForm">
              <b-form-group
                id="input-group-1"
                label="User ID:"
                label-for="input-1"
              >
                <b-form-input id="input-1" v-model="form.userId" :state="null" placeholder="" required></b-form-input>
              </b-form-group>
              <b-form-group
                id="input-group-1"
                label="Password:"
                label-for="input-1"
              >
                <b-form-input id="input-1" v-model="form.password" :state="null" placeholder="" required></b-form-input>
              </b-form-group>
              <b-button type="submit" class="logBtn">LOG IN</b-button>
            </b-form>
          </div>
          <div class="logCardFooter">
            <img :src="thumbnail" class="logCardFooterImg">
          </div>
        </div>
        <div class="welcomeCard" v-if="signin">
          WELCOME!
        </div>
      </div>
    </div>
    <div id="footer">
      edotco Group <span class="footerRight">a communication's infrastructure service company</span>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
export default {
  data() {
    return {
      logData: [],
      signin: false,
      loader: false,
      brandIcon: require('../assets/logo_edotco.svg'),
      bannerImg: require('../assets/edotco_group_banner.png'),
      thumbnail: require('../assets/thumbnail_image.png'),
      form: {
        userId: this.getCookie('user'),
        password: this.getCookie('pass')
      }
    }
  },
  methods: {
    onSubmit(evt) {
      // evt.preventDefault()
      if(this.form.userId == "user" && this.form.password == "1234"){
        this.signin = true;
        this.setCookie('user', this.form.userId, 1)
        this.setCookie('pass', this.form.password, 1)
      }
    },
    setCookie: function(cookie_user, cookie_pass, exdays){
      var d = new Date();
      d.setTime(d.getTime() + (exdays*24*60*60*1000));
      var expires = "expires="+ d.toUTCString();
      document.cookie = cookie_user + "=" + cookie_pass + ";" + expires + ";path=/";
      return true;
    },
    getCookie: function(cookie_name){
      var name = cookie_name + "=";
      var decodedCookie = decodeURIComponent(document.cookie);
      var ca = decodedCookie.split(';');
      for(var i = 0; i <ca.length; i++) {
          var c = ca[i];
          while (c.charAt(0) == ' ') {
              c = c.substring(1);
          }
          if (c.indexOf(name) == 0) {
              return c.substring(name.length, c.length);
          }
      }
      return "";
    },
    removeUser: function(){
      this.setCookie('user', "", 1)
      this.setCookie('pass', "", 1)
      window.location.reload()
    }
  },
  mounted: function() {
    if(this.form.userId != "" && this.form.password != ""){
      this.signin = true;
    }
  }
}
</script>
<style scoped>

/** Navbar Design Start */
#navbar{
  background-color: #fff;
  border-bottom: 1px solid #efefef;
  height: 70px;
  padding: 0 10px;
}
#navbarBrand{
  text-transform: capitalize;
  font-size: 1rem;
  color: #fff;
}
#brandImg{
  height: 45px;
}
/** Navbar Design End */
#mainBody{
  height: 100vh;
  width: 100%;
  background-position: center;
}
#mainContainer{
  padding-top: 70px;
}
#footer{
  width: 100%;
  padding: 10px;
  background-color: #0c754a;
  color: #fff;
  position: fixed;
  bottom: 0;
}
.logCard{
  width: 100%;
  background: #eee;
  position: absolute;
  float: left;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 6px 8px rgba(144, 172, 201, 0.03), 0 1px 2px rgba(102,119,136,.3);
  border-radius: 3px 3px 0 0;
}
.logCardContainer{
  height: 300px;
}
.logCardTitle{
  background-color: #fff;
  /* color: #fff; */
  padding: 15px;
  border-top: 1px solid #efefef;
}
.logCardFooter{
  height: 92px;
  width: 100%;
  bottom: 0;
  background-position: center;
}
.logCardForm{
  padding: 15px;
  color: #000;
}
.logBtn{
  background-color: #aeaeae;
  color: #fff;
  border: none;
}
.logCardFooterImg{
  width: 100%;
}
.footerRight{
  float: right;
  color: #fff;
  text-transform: capitalize;
}
.welcomeCard{
  position: absolute;
  float: left;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 24px;
}
.logOutBtn{
  background-color: #aeaeae;
  border: none;
  outline: none;
}
/** Page Media Query Start */
@media (min-width: 1200px){
  .logCard{
    width: 45%;
  }
}
@media screen and (max-width: 991px) {
  
}
@media screen and (max-width: 767px) {
  
}
@media screen and (max-width: 479px) {
  
}
</style>


